import URL from './routes';

export const getDepts = (
  token
) => new Promise((resolve, reject) => {
  fetch(`${URL}/databases/get_filters/`, {
    method: 'GET',
    headers: {
      Authorization: `Token ${token}`,
    }
  }).then((resultado) => {
    if (resultado.ok) {
      resultado.json().then((res) => resolve(res));
    } else {
      reject("error");
    }
  }).catch((error) => reject(error));
});

